<template>
  <article>
    <h2>Rejestracja</h2>
    <form @submit.prevent="handleRegister">
      <label for="login">Login</label>
      <input
        type="text"
        name="login"
        class="username"
        required
        placeholder="np. Mariusz12"
        v-model="userName"
      />
      <p v-if="loginError" class="error">
        UÅ¼ytkownik o takim loginie juÅ¼ istnieje.
      </p>

      <label for="password">HasÅ‚o</label>
      <input
        type="password"
        name="password"
        class="password"
        required
        placeholder="WprowadÅº hasÅ‚o"
        v-model="password"
      />

      <label for="repeatedPassword">PowtÃ³rz hasÅ‚o</label>
      <input
        type="password"
        name="repeatedPassword"
        class="password"
        required
        placeholder="PowtÃ³rz hasÅ‚o"
        v-model="repeatedPassword"
      />
      <p v-if="passwordMismatchError" class="error">
        HasÅ‚a siÄ™ nie zgadzajÄ…! ProszÄ™ sprÃ³bowaÄ‡ ponownie.
      </p>

      <p v-if="generalError" class="error">ProszÄ™ wypeÅ‚niÄ‡ wszystkie pola.</p>
      <p v-if="registrationSuccess" class="success">
        Rejestracja zakoÅ„czona sukcesem!
      </p>

      <button type="submit" class="submit-btn">Zarejestruj</button>
    </form>
  </article>
</template>

<script>
export default {
  name: "RegisterPage",
  data() {
    return {
      registerData: {
        login: "",
        password: "",
        repeatedPassword: "",
      },
      errorMessage: "",
      successMessage: "",
    };
  },
  methods: {
    async register() {
      try {
        const response = await fetch(
          "http://localhost/notatnik/backend/api/register.php",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(this.registerData),
          }
        );
        const data = await response.json();
        if (response.ok) {
          this.successMessage = data.success;
          this.errorMessage = "";
        } else {
          this.errorMessage = data.error;
          this.successMessage = "";
        }
      } catch (error) {
        this.errorMessage = "WystÄ…piÅ‚ bÅ‚Ä…d podczas rejestracji.";
      }
    },
  },
};
</script>
