<template>
  <nav class="nav nav-open">
    <router-link to="/" class="main-page"><h1>Notatnik.php</h1></router-link>
    <ul class="login">
      <li class="list-link">
        <router-link to="/login" class="link">zaloguj</router-link>
      </li>
      <li class="list-link">
        <router-link to="/register" class="link">zarejestruj</router-link>
      </li>
    </ul>
    <button class="btn-mobile-nav">
      <ion-icon class="icon-mobile-nav" name="menu-outline"></ion-icon>
      <ion-icon class="icon-mobile-nav" name="close-outline"></ion-icon>
    </button>
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      loggedIn: false,
    };
  },
  async mounted() {
    try {
      const response = await fetch(
        "http://localhost/notatnik/backend/api/check_session.php",
        { credentials: "include" }
      );
      const data = await response.json();
      this.loggedIn = data.logged_in;
    } catch (error) {
      console.error("BÅ‚Ä…d podczas sprawdzania sesji:", error);
    }
  },
  methods: {
    async logout() {
      try {
        const response = await fetch(
          "http://localhost/notatnik/backend/api/logout.php",
          { method: "POST", credentials: "include" }
        );
        if (response.ok) {
          this.loggedIn = false;
          this.$router.push("/");
        }
      } catch (error) {
        console.error("BÅ‚Ä…d podczas wylogowywania:", error);
      }
    },
  },
};
</script>
