<template>
  <div>
    <h2>Twoje notatki</h2>
    <div v-if="notes.length">
      <div v-for="note in notes" :key="note.id" class="note">
        <h3>{{ note.title }}</h3>
        <p>{{ note.content }}</p>
        <button @click="editNote(note.id)">Edytuj</button>
        <button @click="deleteNote(note.id)">UsuÅ„</button>
      </div>
    </div>
    <p v-else>Brak notatek. Dodaj nowÄ… notatkÄ™!</p>
    <button @click="$router.push('/add')">Dodaj notatkÄ™</button>
  </div>
</template>

<script>
export default {
  name: "NotesList",
  data() {
    return {
      notes: [],
    };
  },
  async mounted() {
    const response = await fetch(
      "http://localhost/notatnik/backend/api/get_notes.php",
      {
        credentials: "include",
      }
    );
    this.notes = await response.json();
  },
  methods: {
    editNote(id) {
      this.$router.push(`/edit/${id}`);
    },
    async deleteNote(id) {
      if (confirm("Czy na pewno chcesz usunÄ…Ä‡ tÄ™ notatkÄ™?")) {
        await fetch("http://localhost/notatnik/backend/api/delete_note.php", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ note_id: id }),
          credentials: "include",
        });
        this.notes = this.notes.filter((note) => note.id !== id);
      }
    },
  },
};
</script>
