<template>
  <article>
    <h2>Logowanie</h2>
    <form @submit.prevent="handleSubmit">
      <label for="text">Login</label>
      <input
        type="text"
        name="login"
        class="username"
        required
        placeholder="np. Mariusz"
        v-model="userName"
      />
      <p v-if="errorMessage.includes('login')" class="error">
        {{ errorMessage }}
      </p>

      <label for="password">HasÅ‚o</label>
      <input
        type="password"
        name="password"
        class="password"
        required
        placeholder="WprowadÅº hasÅ‚o"
        v-model="password"
      />
      <p v-if="errorMessage.includes('hasÅ‚o')" class="error">
        {{ errorMessage }}
      </p>

      <p v-if="errorMessage.includes('wszystkie pola')" class="error">
        {{ errorMessage }}
      </p>

      <button type="submit" class="submit-btn">Zaloguj</button>
    </form>
  </article>
</template>

<script>
export default {
  name: "LoginPage",
  data() {
    return {
      loginData: {
        login: "",
        password: "",
      },
      errorMessage: "",
    };
  },
  methods: {
    async login() {
      try {
        const response = await fetch(
          "http://localhost/notatnik/backend/api/login.php",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(this.loginData),
            credentials: "include",
          }
        );
        const data = await response.json();
        if (response.ok) {
          this.$router.push("/notes");
        } else {
          this.errorMessage = data.error;
        }
      } catch (error) {
        this.errorMessage = "WystÄ…piÅ‚ bÅ‚Ä…d podczas logowania.";
      }
    },
  },
};
</script>
